import React, { Component } from 'react';

import Layout from './components/Layout/Layout';
import axios from 'axios';
import OverallRanking from './containers/OveralRanking/OverallRanking';
import Leaderboard from './containers/Leaderboards/Leaderboard';
import TeamRankings from './containers/TeamRankings/TeamRankings';
import Socialwall from './containers/socialwall/socialwall';
import logo from './img/ew-games-logo.png';
import './App.css';

//Here we are bringing in the Carousel component from Bootstrap
var Carousel = require('react-bootstrap/lib/Carousel');

class App extends Component {

    state= {

        teamRanking: true,
        individualRanking : true,
        Red: true,
        Blue : true,
        Purple : true,
        Teal : true,
        Grey : true,
        Socialwall : true,
        data:[],
        controls: false,
        indicators: true,

    }

    componentDidMount(){



        setInterval( () => {

            axios.get('http://otew.io/api/points/grabPoints')
                .then(response => {

                    this.setState({data : response.data});

                });

            axios.get('http://otew.io/api/leaderboard/screens')
                .then(response => {


                    this.setState({teamRanking:response.data[0].status})
                    this.setState({individualRanking:response.data[1].status})
                    this.setState({Red:response.data[2].status})
                    this.setState({Blue:response.data[3].status})
                    this.setState({Purple:response.data[4].status})
                    this.setState({Teal:response.data[5].status})
                    this.setState({Grey:response.data[6].status})
                    this.setState({Socialwall:response.data[7].status})


                });


        },2000)


    }




    render() {





    return (
      <div className="App">
       <header><img src={logo}  alt="The EW Games" /></header>
          <Layout>

              <Carousel
              controls={this.state.controls}
              indicators={this.state.indicators}
              >
                {this.state.teamRanking ?       
				<Carousel.Item>
                          <div className="item" >

                              <OverallRanking data={this.state.data}/>

                          </div>
                </Carousel.Item> : null
                }


                     {this.state.individualRanking ?
                      <Carousel.Item>
                          <div className="item">

                              <Leaderboard data={this.state.data}/>
                          </div> 
                          </Carousel.Item>: null
                    }
                    
                    {this.state.Red ?
                    <Carousel.Item>
                      <div className="item">
                          <TeamRankings team="Red" data={this.state.data}/>
                      </div>
                      </Carousel.Item>: null 
                      }
                      {this.state.Purple ?
                      <Carousel.Item>
                      <div className="item">
                          <TeamRankings team="Purple" data={this.state.data}/>
                      </div>
                      </Carousel.Item>: null
                      }
                      
                      {this.state.Blue ? 
                      <Carousel.Item>
                      <div className="item">
                          <TeamRankings team="Blue" data={this.state.data}/>
                      </div>
                      </Carousel.Item>: null
                      }
                      {this.state.Teal ? 
                      <Carousel.Item>
                      <div className="item">
                          <TeamRankings team="Teal" data={this.state.data}/>
                      </div>
                      </Carousel.Item>: null
                      }
                      {this.state.Grey ?
                      <Carousel.Item>
                      <div className="item">
                          <TeamRankings team="Grey" data={this.state.data}/>
                      </div>
					</Carousel.Item>: null
					}
					{this.state.Socialwall ?
					<Carousel.Item>
                      <div className="item">
                         <Socialwall/>
                      </div>
					</Carousel.Item>: null
					}

                 </Carousel>



          </Layout>

      </div>
    );
  }
}

export default App;



