import React, {Component} from 'react';
import axios from 'axios';
import Aux from '../../hoc/hoc';
import classes from './OverallRanking.css'

import Teams from '../../components/OverallRanking/Teams/Teams'




class OverallRanking extends Component{

    state={
        Red : "",
        Blue: "",
        Purple: "",
        Teal:"",
        Grey:"",
        curTime : null
    }



     componentDidMount(){

         setInterval( () => {
        console.log("this property is passed from App.js :## = " + JSON.stringify(this.props.data.overallScores));
             this.setState({Red: this.props.data.overallScores.Red})
             this.setState({Blue: this.props.data.overallScores.Blue})
             this.setState({Purple: this.props.data.overallScores.Purple})
             this.setState({Teal: this.props.data.overallScores.Teal})
             this.setState({Grey: this.props.data.overallScores.Grey})
         },5000)









    }


    render(){




        return(
            <Aux>

                <div id="overallTeam" className="cycle-slide leaderboard" data-cycle-timeout="20000">

                    <h1>Overall team rankings</h1>



                    <div className="rows">


                        <Teams name="Red" badge="teamIcon red" score={this.state.Red}/>
                        <Teams name="Blue" badge="teamIcon blue" score={this.state.Blue}/>
                        <Teams name="Purple" badge="teamIcon purple" score={this.state.Purple}/>
                        <Teams name="Teal" badge="teamIcon teal" score={this.state.Teal}/>
                        <Teams name="Grey" badge="teamIcon grey" score={this.state.Grey}/>




                    </div>

                </div>

            </Aux>
        );
    }
}
export default OverallRanking



