import React from 'react';




const tweet = (props) => {

    return (

                <div className="socialPost noTeamPost sourceTwitter" >
                    <span className="username">{props.screenName}</span>
                    <span className="message">{props.tweetText}</span>

                </div>


    );
};

export default tweet



