import React, {Component} from 'react';
import axios from 'axios';
import Aux from '../../hoc/hoc';
import classes from './TeamRanking.css'


import TeamBoards from '../../components/TeamRankings/TeamRankings';




class TeamRankings extends Component{

    state={
        first : [],
        second: [],
        third: [],
        fourth:[],
        fifth:[],
        teamIcon:"",
    }

    componentDidMount(){

        let team = this.props.team;


        console.log("team is " +team);


        setInterval( () => {
            // console.log("this property is passed from App.js :## = " + JSON.stringify(this.props.data.overallScores));

            if(team==='Red'){


                this.setState({first: this.props.data.teamLeaders.Red[0]})
                this.setState({second: this.props.data.teamLeaders.Red[1]})
                this.setState({third: this.props.data.teamLeaders.Red[2]})
                this.setState({fourth: this.props.data.teamLeaders.Red[3]})
                this.setState({fifth: this.props.data.teamLeaders.Red[4]})
                this.setState({teamIcon:'red'})
            }

            else if(team==='Blue'){


                this.setState({first: this.props.data.teamLeaders.Blue[0]})
                this.setState({second: this.props.data.teamLeaders.Blue[1]})
                this.setState({third: this.props.data.teamLeaders.Blue[2]})
                this.setState({fourth: this.props.data.teamLeaders.Blue[3]})
                this.setState({fifth: this.props.data.teamLeaders.Blue[4]})
                this.setState({teamIcon:'blue'})
            }

            else if(team==='Purple'){

                this.setState({first: this.props.data.teamLeaders.Purple[0]})
                this.setState({second: this.props.data.teamLeaders.Purple[1]})
                this.setState({third: this.props.data.teamLeaders.Purple[2]})
                this.setState({fourth: this.props.data.teamLeaders.Purple[3]})
                this.setState({fifth: this.props.data.teamLeaders.Purple[4]})
                this.setState({teamIcon:'purple'})

            }
            else if(team==='Teal'){


                this.setState({first: this.props.data.teamLeaders.Teal[0]})
                this.setState({second: this.props.data.teamLeaders.Teal[1]})
                this.setState({third: this.props.data.teamLeaders.Teal[2]})
                this.setState({fourth: this.props.data.teamLeaders.Teal[3]})
                this.setState({fifth: this.props.data.teamLeaders.Teal[4]})
                this.setState({teamIcon:'teal'})


            }
            else if(team==='Grey'){

                console.log("this is grey team ^^^^^^^");

                this.setState({first: this.props.data.teamLeaders.Grey[0]})
                this.setState({second: this.props.data.teamLeaders.Grey[1]})
                this.setState({third: this.props.data.teamLeaders.Grey[2]})
                this.setState({fourth: this.props.data.teamLeaders.Grey[3]})
                this.setState({fifth: this.props.data.teamLeaders.Grey[4]})
                this.setState({teamIcon:'grey'})

            }



        },5000)










    }


    render(){





        return(
            <Aux>

                <div id={'team' + this.props.team} className="cycle-slide leaderboard team" data-cycle-timeout="7000">



                    <div className="rows-teams">

                        <h1>{this.props.team} team rankings</h1>


                        <div className="teamBadge"> </div>
<div className="rows">
                        <TeamBoards teamIcon={'teamIcon ' + this.state.teamIcon}
                                    rank={'1'}
                                    CName={'row '+ this.state.teamIcon+'0'}
                                    name={JSON.stringify(this.state.first['name'])}
                                    score={JSON.stringify(this.state.first['total'])}
                        />
                        <TeamBoards teamIcon={'teamIcon ' + this.state.teamIcon}
                                    rank={'2'}
                                    CName={'row '+ this.state.teamIcon+'0'}
                                    name={JSON.stringify(this.state.second['name'])}
                                    score={JSON.stringify(this.state.second['total'])}
                        />
                        <TeamBoards teamIcon={'teamIcon ' + this.state.teamIcon}
                                    rank={'3'}
                                    CName={'row '+ this.state.teamIcon+'0'}
                                    name={JSON.stringify(this.state.third['name'])}
                                    score={JSON.stringify(this.state.third['total'])}
                        />
                        <TeamBoards teamIcon={'teamIcon ' + this.state.teamIcon}
                                    rank={'4'}
                                    CName={'row '+ this.state.teamIcon+'0'}
                                    name={JSON.stringify(this.state.fourth['name'])}
                                    score={JSON.stringify(this.state.fourth['total'])}
                        />
                        <TeamBoards  teamIcon={'teamIcon ' + this.state.teamIcon}
                                     rank={'5'}
                                     CName={'row '+ this.state.teamIcon+'0'}
                                     name={JSON.stringify(this.state.fifth['name'])}
                                     score={JSON.stringify(this.state.fifth['total'])}
                        />

</div>
                    </div>

                </div>

            </Aux>
        );
    }
}
export default TeamRankings



