import React, {Component} from 'react';
import axios from 'axios';
import Aux from '../../hoc/hoc';
import Classes from './Leaderboard.css'


import Teams from '../../components/OverallRanking/Teams/Teams'




class OverallRanking extends Component{

    state={
        first : [],
        second: [],
        third: [],
        fourth:[],
        fifth:[],
    }

    componentDidMount(){


        setInterval( () => {
           // console.log("this property is passed from App.js :## = " + JSON.stringify(this.props.data.overallScores));
            this.setState({first: this.props.data.overallLeaders[0]})
            this.setState({second: this.props.data.overallLeaders[1]})
            this.setState({third: this.props.data.overallLeaders[2]})
            this.setState({fourth: this.props.data.overallLeaders[3]})
            this.setState({fifth: this.props.data.overallLeaders[4]})

        },5000)





    }


    render(){





        return(
            <Aux>
                <h1>I am here </h1>

                <div id="overallTeam" className="cycle-slide leaderboard" data-cycle-timeout="7000">

                    <h1>Leaders</h1>

                    <div className="rows">



                        <Teams name={JSON.stringify(this.state.first['name'])} score={JSON.stringify(this.state.first['total'])}/>
                        <Teams name={JSON.stringify(this.state.second['name'])} score={JSON.stringify(this.state.second['total'])}/>
                        <Teams name={JSON.stringify(this.state.third['name'])} score={JSON.stringify(this.state.third['total'])}/>
                        <Teams name={JSON.stringify(this.state.fourth['name'])} score={JSON.stringify(this.state.fourth['total'])}/>
                        <Teams name={JSON.stringify(this.state.fifth['name'])} score={JSON.stringify(this.state.fifth['total'])}/>


                    </div>

                </div>

            </Aux>
        );
    }
}
export default OverallRanking



