import React, {Component} from 'react';
import axios from 'axios';
import Aux from '../../hoc/hoc';
import Tweets from '../../components/Socialwall/tweets/tweets'
import classes from './socialwall.css'



class Socialwall extends Component{

    state={
        tweets: []
    }

    componentDidMount(){

        setInterval( () => {

       axios.get('http://otew.io/api/tweets/getTweets')
          .then(response => {
              this.setState({tweets:response.data})
              console.log("in tweets");
          });

        },5000)
    }

    render(){

        const tweets = this.state.tweets.map(tweet=>{

            return <Tweets  key={tweet.tweet_id}
                            screenName={tweet.screen_name}
                            tweetText={tweet.tweet_text} />
        });


        return(
            <Aux>

                <div id="socialwall">


                        <div id="socialWallContainer ">

                            {tweets}
                        </div>

                </div>

            </Aux>
        );
    }
}
export default Socialwall



