import React from 'react';
import classes from './Teams.css'


const Teams = (props) => {

    return (

        <div className="row" id="rdPoints" data-points="21342134">
            <div className="rank"></div>
            <div className={props.badge}></div>
            <div className="name">{props.name}</div>
            <div className="points">{props.score}</div>
        </div>

    );
};

export default Teams


